import androidx.compose.runtime.CompositionLocalProvider
import com.varabyte.kobweb.core.AppGlobalsLocal
import com.varabyte.kobweb.navigation.RoutePrefix
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.silk.components.animation.registerKeyframes
import kotlin.Unit
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable

public fun main(): Unit {
    RoutePrefix.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.register("/") { com.tessanix.pages.HomePage() }
        ctx.router.register("/karaokeappproject") { com.tessanix.pages.KaraokeAppProjectPage() }
        ctx.router.register("/kobwebsiteproject") { com.tessanix.pages.KobwebSiteProjectPage() }
        ctx.router.register("/qtvpnproject") { com.tessanix.pages.QtVpnProject() }

    }
    com.varabyte.kobweb.silk.init.initSilkHook = { ctx ->
        ctx.theme.registerComponentStyle(com.tessanix.components.ContainerTextStyle)
        ctx.theme.registerComponentStyle(com.tessanix.components.NavStyle)
        ctx.theme.registerComponentStyle(com.tessanix.components.MenuTitleUnderlinedStyle)
        ctx.theme.registerComponentStyle(com.tessanix.components.HamburgerStyle)
        ctx.theme.registerComponentStyle(com.tessanix.components.MagnifyingGlassIconStyle)
        ctx.theme.registerComponentStyle(com.tessanix.components.MagnifyingGlassIconGrow)
        ctx.theme.registerComponentStyle(com.tessanix.components.ScrollDownAnimationStyle)
        ctx.theme.registerComponentStyle(com.tessanix.components.TextSkillsStyle)
        ctx.theme.registerComponentStyle(com.tessanix.components.ContainerSliderStyle)
        ctx.theme.registerComponentStyle(com.tessanix.components.SliderStyle)
        ctx.theme.registerComponentStyle(com.tessanix.components.CheckboxToggleButtonStyle)
        ctx.theme.registerComponentStyle(com.tessanix.components.DivToggleButtonStyle)
        ctx.theme.registerComponentStyle(com.tessanix.pages.InputStyle)
        ctx.theme.registerComponentStyle(com.tessanix.pages.ImageContainerProjectStyle)
        ctx.theme.registerComponentStyle(com.tessanix.pages.ImageProjectStyle)
        ctx.theme.registerComponentStyle(com.tessanix.pages.TitleUnderlinedStyle)
        ctx.theme.registerComponentVariants(com.tessanix.components.NavColumnStyle)
        ctx.theme.registerComponentVariants(com.tessanix.components.MenuTitleUnderlinedGrow)
        ctx.theme.registerComponentVariants(com.tessanix.components.MenuTitleUnderlinedShrink)
        ctx.theme.registerComponentVariants(com.tessanix.components.DivToggleFrenchBefore)
        ctx.theme.registerComponentVariants(com.tessanix.components.DivToggleEnglishBefore)
        ctx.stylesheet.registerKeyframes(com.tessanix.components.CursorBlinkingAnimation)
        ctx.stylesheet.registerKeyframes(com.tessanix.components.TypingCursorAnimation)
        ctx.stylesheet.registerKeyframes(com.tessanix.components.moveDowArrow)
        com.tessanix.initSilk(ctx)
    }

    router.navigateTo(window.location.href.removePrefix(window.location.origin))

    // For SEO, we may bake the contents of a page in at build time. However, we will overwrite them
    // the first time we render this page with their composable, dynamic versions. Think of this as
    // poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    val root = document.getElementById("root")!!
    while (root.firstChild != null) {
        root.removeChild(root.firstChild!!)
    }

    renderComposable(rootElementId = "root") {
        CompositionLocalProvider(
            AppGlobalsLocal provides mapOf("title" to "Portfolio")
        ) { com.tessanix.MyApp {
                com.varabyte.kobweb.silk.defer.renderWithDeferred { router.renderActivePage() }
            }
        }
    }
}
